import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      redirect: '/dashboard'
    },

    {
      path: '/',
      component: resolve => require(['../components/common/Home.vue'], resolve),
      meta: {title: '自述文件'},
      children: [
        {
          path: '/banner',
          component: resolve => require(['../components/AdminControl/index/img/list.vue'], resolve),
          meta: { title: 'banner' }
        },
        {
          path: '/geren',
          component: resolve => require(['../components/AdminControl/tijian/geren.vue'], resolve),
          meta: { title: '个人体检' }
        },
        {
          path: '/type',
          component: resolve => require(['../components/AdminControl/tijian/geren-L2.vue'], resolve),
          meta: { title: '体检-分类' }
        },
        {
          path: '/testD',
          component: resolve => require(['../components/AdminControl/tijian/gerenD.vue'], resolve),
          meta: { title: '体检-详情' }
        },
        {
          path: '/testC',
          component: resolve => require(['../components/AdminControl/tijian/gerenC.vue'], resolve),
          meta: { title: '体检-编辑' }
        },
        {
          path: '/team',
          component: resolve => require(['../components/AdminControl/tijian/team.vue'], resolve),
          meta: { title: '团队体检' }
        },
        {
          path: '/ruzhi',
          component: resolve => require(['../components/AdminControl/tijian/ruzhi.vue'], resolve),
          meta: { title: '入职体检' }
        },
        {
          path: '/dashboard',
          component: resolve => require(['../components/page/Dashboard.vue'], resolve),
          meta: {title: 'index',}
        },
        {
          path: '/qyinfo',
          component: resolve => require(['../components/AdminControl/qyinfo/qyinfo.vue'], resolve),
          meta: { title: '企业信息' }
        },
        {
          path: '/recruit',
          component: resolve => require(['../components/AdminControl/recruit/recruit.vue'], resolve),
          meta: { title: '招聘信息' }
        },
        {
          path: '/recruitD',
          component: resolve => require(['../components/AdminControl/recruit/recruitD.vue'], resolve),
          meta: { title: '招聘信息-详情' }
        },
        {
          path: '/recruitC',
          component: resolve => require(['../components/AdminControl/recruit/recruitC.vue'], resolve),
          meta: { title: '招聘信息-编辑' }
        },

        {
          path: '/article',
          component: resolve => require(['../components/AdminControl/article/article.vue'], resolve),
          meta: { title: '文章' }
        },
        {
          path: '/articleD',
          component: resolve => require(['../components/AdminControl/article/articleD.vue'], resolve),
          meta: { title: '文章-详情' }
        },
        {
          path: '/articleC',
          component: resolve => require(['../components/AdminControl/article/articleC.vue'], resolve),
          meta: { title: '文章-编辑' }
        },
        {
          path: '/articleAdd',
          component: resolve => require(['../components/AdminControl/article/articleAdd.vue'], resolve),
          meta: { title: '文章-新增' }
        },
        {
          path: '/404',
          component: resolve => require(['../components/page/404.vue'], resolve),
          meta: {title: '404'}
        },
        {
          path: '/403',
          component: resolve => require(['../components/page/403.vue'], resolve),
          meta: {title: '403'}
        }
      ]
    },
    {
      path: '/login',
      component: resolve => require(['../components/page/login/Login.vue'], resolve)
    },
    {
      path: '*',
      redirect: '/404'
    }
  ]
})



